.Navigationbar {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.Navigationbar-direction-right {
  right: 0px;
  top: 0px;
  width: 60px;
  height: 100%;
}
.Navigationbar-direction-bottom {
  right: 0px;
  bottom: 0px;
  width: 100%;
  height: 60px;
}
.Navigationbar-hide {
  display: none;
}
.Navigationbar-show {
  display: block;
}
.Navigationbar-showButton {
  position: fixed;
  right: 0px;
  width: 60px;
  height: 60px;
  bottom: 10px;
  background-color: #f7f7f7;
  border-radius: 10px 0 0 10px;
  border-top: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
}
.Navigationbar-container {
  width: 100%;
  background-color: #f7f7f7;
  overflow: hidden;
}
.Navigationbar-container-direction-right {
  border-radius: 10px 0 0 10px;
  border-top: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}
.Navigationbar-container-direction-bottom {
  border-radius: 10px 10px 0 0;
  border-top: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  display: -ms-flexbox;
  display: flex;
  max-width: 300px;
}
.Navigationbar-container-item {
  height: 60px;
  line-height: 60px;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  font-size: 12px;
  font-weight: 200;
}
.Navigationbar-container-item > img {
  width: 35%;
}



