.Detail-QWModal {
  margin: 0 auto;
}
.Detail-QWModal-header {
  font-size: 36px;
  text-align: center;
  line-height: 100px;
}
.Detail-QWModal-content {
  width: 95%;
  margin: 0 auto;
}
.Detail-QWModal-footer {
  height: 50px;
  text-align: center;
}
.Detail-QWModal-button {
  border-radius: 1px;
  margin-right: 10px;
  border: 1px solid #ddd;
}
.Detail-QWModal-button:hover {
  background-color: #001529;
  border: 1px solid #001529;
  color: #fff;
}
.Detail-QWModal-button:focus {
  background-color: #001529;
  border: 1px solid #001529;
  color: #fff;
}
.Detail-QWModal-button:active {
  background-color: #001529;
  border: 1px solid #001529;
  color: #fff;
}



